@import "../../../assets/styles/media.scss";
@import "../../../assets/styles/mixins.scss";

.accordionContainer {
  margin-top: 42px;
}

.container {
  margin: 200px 0 70px;
  padding-top: 70px;
  @media screen and (max-width: $desktop) {
    margin: 50px 0 120px;
    padding-top: 0;
  }
}

.greenText {
  text-transform: uppercase;
  position: relative;

  background: linear-gradient(90deg, #FBBF9A -4.09%, #FFE876 9.96%, #12F9F8 28.24%, #9CFD82 55.1%, #FCF16E 78.52%, #FED292 105.6%), #B7F201;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.paragraph {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  text-align: right;
  margin-left: auto;
  font-size: 16px;
  color: #C1C0C0;
  margin-right: 40px;

  @media screen and (min-width: $m-desktop) {
    width: 65%;
  }

}

.content {
  padding: 0 20px;
}

.titleWrapper {
  display: grid;
  position: relative;
  grid-template-columns: 49.6% 50.4%;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  @media screen and (min-width: $m-desktop) {
    padding: 0 120px;
  }

}

.title {
  line-height: 120%;
  letter-spacing: 0.03em;
  font-size: 64px;
  margin-left: 43px;
}


.tableWrapper {
  position: relative;
}

.table {
  display: grid;
  position: relative;
  grid-template-columns: 1fr 1fr;
  margin-top: 87px;

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 1px;
    opacity: 0.5;
    background: linear-gradient(90deg, rgba(48, 41, 33, 1) 0%, rgba(30, 46, 39, 1) 20%, rgba(16, 47, 45, 1) 33%, rgba(33, 56, 38, 1) 50%, rgba(38, 46, 29, 1) 69%, rgba(47, 45, 27, 1) 84%, rgba(105, 145, 84, 1) 100%);
  }
}

@media screen and (max-width: $desktop) {
  .titleWrapper {
    padding: 0 20px;
    grid-template-columns: 1fr;
  }

  .title {
    @include prtnlSclScrnSize(font-size, 70);
    @include prtnlSclScrnSize(margin-right, 40);
    order: 1;
  }

  .paragraph {
    display: flex;
    order: 2;
    margin-top: 16px;
    flex-wrap: wrap;
    justify-content: flex-start;
    @include prtnlSclScrnSize(font-size, 23);
    @include prtnlSclScrnSize(margin-right, 40);
  }
}

@media screen and (max-width: $tablet) {

  .title {
    font-size: 32px;
    padding-right: 15px;
    width: 100%;
    text-align: left;
    margin-left: 28px;
  }

  .paragraph {
    font-size: 14px;
    margin-top: 0;
    text-align: left;
    @include prtnlSclScrnSize(margin-top, 90);
  }
}

@media screen and (max-width: $m-mobile) {
  .titleWrapper {
    grid-template-columns: 1fr;
  }

  .title {
    order: 1;
    font-size: 36px;
  }

  .paragraph {
    order: 2;
    font-size: 14px;
    text-align: left;
  }
}
