@import "~assets/styles/media.scss";

// bio title
$color: #50545A;
.blockInfoTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;

  hr {
    width: 100%;
    display: block; 
    height: 1px;
    border: 0; 
    border-top: 1px solid $color;
  }
}

.blockInfo {
  font-family: 'Hanson', sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: $color;
}

@media screen and (max-width: $m-desktop) {
	.blockInfo {
    font-size: 20px;
	}
}

@media screen and (max-width: $desktop) {
	.blockInfo {
    font-size: 24px;
	}
}

@media screen and (max-width: $tablet) {
	.blockInfo {
    font-size: 20px;
	}

  .blockInfoTitle {
    margin-bottom: 11px;
  }
}
