@import '~assets/styles/media.scss';

.container {
	padding: 0 20px;
	position: relative;

	@media screen and (min-width: $l-desktop) {
		padding: 170px 130px;
	}

	@media screen and (min-width: $desktop) {
		display: grid;
		grid-template-columns: 1fr 1fr;
		align-items: center;

		padding: 170px 20px;
	}
}

.cardsImg {
	display: flex;
	justify-content: center;
	align-items: center;
}

$margin-left: 26px;
.chineseText {
	font-family: 'DFLiKingHei1B', sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 23px;
	letter-spacing: 0.1em;
	color: #ffffff;
	margin: 0;

	@media screen and (min-width: $desktop) {
		font-size: 24px;
		line-height: 27px;
		margin: $margin-left 0 0 27px;
	}
}

.titleWrapper {
	margin: $margin-left 0 24px 0;
}

.title {
	font-size: 36px;
	line-height: 120%;
	letter-spacing: 0.03em;
	margin-left: 28px;

	@media screen and (min-width: $desktop) {
		font-size: 64px;
		line-height: 1;
	}
}

.morhTitle {
	font-family: 'Hanson', sans-serif;
	font-size: 24px;
	line-height: 120%;
	color: #ffffff;
	text-transform: uppercase;
	letter-spacing: 0.03em;
	margin-bottom: 24px;

	@media screen and (min-width: $desktop) {
		font-size: 36px;
		text-align: left;
		margin: 74px 0 16px 26px;
	}
}

.morhDescr {
	font-size: 14px;
	margin-left: 0;
	color: #c1c0c0;
	line-height: 160%;

	@media screen and (min-width: $desktop) {
		margin-left: $margin-left;
		max-width: 457px;
		font-size: 16px;
	}
}

.greenText {
	text-transform: uppercase;
	position: relative;

	background: linear-gradient(
			90deg,
			#fbbf9a -4.09%,
			#ffe876 9.96%,
			#12f9f8 28.24%,
			#9cfd82 55.1%,
			#fcf16e 78.52%,
			#fed292 105.6%
		),
		#b7f201;
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
}
.waysToStartParagraph {
	font-size: 16px;
	margin-bottom: 32px;
}

.waysToStartChinese {
	display: block;
	font-family: 'DFLiKingHei1B', sans-serif;
	font-size: 20px;
	line-height: 20px;
	letter-spacing: 0.1em;
	text-align: center;
	color: #ffffff;
	margin-bottom: 12px;
	margin-top: 32px;
	@media screen and (min-width: $desktop) {
		display: none;
	}
}

.comparingVideo {
	height: 562px;
}
